/* Unified theme CSS for both dark and light mode gradients */

:root {
  --task-gradient-text: linear-gradient(90deg, #ff9800 0%, #ffd580 100%);
}

.wrapper.darkmode {
  --task-gradient-text: linear-gradient(90deg, #8854d0 0%, #a18cd1 100%);
}

/* --- LIGHT MODE (default) --- */
.clear-btn {
  background: linear-gradient(90deg, #ff9800 0%, #ffd580 100%) !important;
  border: 1.5px solid #ff9800 !important;
  color: #fff !important;
}
.clear-btn:disabled {
  background: #eee !important;
  color: #aaa !important;
  border: 1.5px solid #ff9800 !important;
}
.settings-popup button {
  background: linear-gradient(90deg, #ff9800 0%, #ffd580 100%) !important;
  border: none !important;
  color: #fff !important;
}
.settings-popup .toggle-switch-track {
  background: linear-gradient(90deg, #ff9800 0%, #ffd580 100%) !important;
}
.page-navigator {
  border: 1px solid #ff9800 !important;
}
.page-navigator button {
  border: 1px solid #ff9800 !important;
  background: linear-gradient(90deg, #ff9800 0%, #ffd580 100%) !important;
  color: #fff !important;
}
.page-navigator button[disabled],
.page-navigator button:disabled {
  background: #eee !important;
  color: #aaa !important;
}

/* --- DARK MODE --- */
.wrapper.darkmode {
  background: #232946 !important;
  color: #fff !important;
}
.wrapper.darkmode .task-input input {
  background: #232946 !important;
  color: #fff !important;
  border: 1.5px solid #a18cd1 !important;
}
.wrapper.darkmode .task-input input:focus,
.wrapper.darkmode .task-input input.active {
  border: 2px solid #a18cd1 !important;
}
.wrapper.darkmode .controls {
  border-bottom: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .clear-btn {
  background: linear-gradient(90deg, #8854d0 0%, #a18cd1 100%) !important;
  border: 1.5px solid #a18cd1 !important;
  color: #fff !important;
}
.wrapper.darkmode .clear-btn:disabled {
  background: #444 !important;
  color: #aaa !important;
  border: 1.5px solid #a18cd1 !important;
}
.wrapper.darkmode .settings-popup button {
  background: linear-gradient(90deg, #8854d0 0%, #a18cd1 100%) !important;
  color: #fff !important;
}
.wrapper.darkmode .settings-popup .toggle-switch-track {
  background: linear-gradient(90deg, #8854d0 0%, #a18cd1 100%) !important;
}
.wrapper.darkmode .page-navigator {
  border: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .page-navigator button {
  border: 1px solid #a18cd1 !important;
  background: linear-gradient(90deg, #8854d0 0%, #a18cd1 100%) !important;
  color: #fff !important;
}
.wrapper.darkmode .page-navigator button[disabled],
.wrapper.darkmode .page-navigator button:disabled {
  background: #444 !important;
  color: #aaa !important;
}

/* --- DARK MODE: Task Menu --- */
.wrapper.darkmode .task {
  background: #232946 !important;
  color: #fff !important;
  border-bottom: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .task .checked {
  color: #a18cd1 !important;
}
.wrapper.darkmode .task-menu {
  background: #232946 !important;
  color: #fff !important;
  border: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .task-menu.show {
  background: #232946 !important;
  color: #fff !important;
  border: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .open-task-menu,
.wrapper.darkmode .open-task-menu.active {
  background: #232946 !important;
  color: #a18cd1 !important;
  border: 1px solid #a18cd1 !important;
}
.wrapper.darkmode .settings {
  background: #232946 !important;
  color: #fff !important;
}
.wrapper.darkmode .uil {
  color: #a18cd1 !important;
}

